:root{
  --clr-primary: #5d5c61;
  --clr-primary-2: #4e4e50;
  --clr-primary-3: #7395ae;
  --clr-primary-4: #557a95;
  --clr-primary-5: #b1a296;


}
body {
  margin: 0;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(45deg, var(--clr-primary-4) 10%, var(--clr-primary-2) 60%);
  color: var(--clr-primary-3);
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:link{
  color: var(--clr-primary-4);
  text-decoration: none;
  padding: 10px;
}
:visited{
  color: var(--clr-primary-5);
}
.navigation{
  margin-top: 25px;
  display: flex;
  justify-content: space-between;
  

}
header h1{
  margin-left: 25px;
}
aside ul li{
  list-style: none;
  line-height: 2;
}
aside ul li button{
  background-color: var(--clr-primary);
  font-size: 1rem;
  color: #7395ae;
  margin-left: 10px;
}
aside form{
  line-height: 2;
}
.email-form{
  display: none;
}
.profile {
  max-width: 30%;
  max-height: 50%;
  border-radius: 50%;
}
.pagelinks{
  list-style: none;
}
.contact-form{
  display: flex;
  align-items: center;
  justify-content: center;
}
form {
  background-color: #15172b;
  border-radius: 20px;
  box-sizing: border-box;
  height: 500px;
  padding: 20px;
  width: 320px;

}
.input-container {
  height: 50px;
  position: relative;
  width: 100%;
}
.ic1 {
  margin-top: 40px;
}

.ic2 {
  margin-top: 30px;
}
.input {
  background-color: #303245;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  font-size: 18px;
  height: 100%;
  outline: 0;
  padding: 4px 20px 0;
  width: 100%;
}
.submit {
  background-color: #08d;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  cursor: pointer;
  font-size: 18px;
  height: 50px;
  margin-top: 38px;
  outline: 0;
  text-align: center;
  width: 100%;
}

.submit:active {
  background-color: #06b;
}
figure{
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  grid-template-rows: 'div div';
  

}
.picture img{
  max-width: 75%;
  max-height: 75%;
}
.picture a:hover{
  transform: scale(1.2);
}
footer p{
  font-size: 12px;
  color: #b1a296;
}

